import React, { Component } from "react";
import { Field, reduxForm } from "redux-form";
import { connect } from "react-redux";
import Stepper from "react-stepper-horizontal";
import paginationFactory from "react-bootstrap-table2-paginator";
import {
  getDataSelectedValidasiSupplier,
  getValidasiSupplierPembayaranHutangSupplier,
  getValidasiSupplierPenerimaanBarangSupplier,
  getValidasiSupplierReturnSupplier,
  getValidasiSupplierTitipSupplier,
  getValidasiSupplierTolakanPembayaranSupplier,
  getValidasiSupplierTolakanReturnSupplier,
} from "../actions/validasiSupplierAction";
// import { addFaktur, checkReplikasiValidasi, deleteFaktur } from "../FindIndex";
import Axios from "axios";
import Swal from "sweetalert2";
import { gagal } from "../Alert";
import HeaderCard from "../HeaderCard";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { AxiosMasterPut } from "../Axios";

const renderField = ({
  input,
  label,
  type,
  limit,
  readOnly,
  meta: { touched, error, warning },
}) => (
  <div className="form-group">
    <label htmlFor="" className="text-black">
      {label}
    </label>
    <input
      {...input}
      type={type}
      className="form-control"
      min="0"
      step="0.001"
      maxLength={limit}
      readOnly={readOnly}
    />
    {touched &&
      ((error && <p className="invalid-feedback">{error}</p>) ||
        (warning && <p>{warning}</p>))}
  </div>
);

const maptostate = (state) => {
  return {
    listValidasiSupplier: state.provinsi.listValidasiSupplier,
    listDataSelectedValidasiSupplier:
      state.provinsi.listDataSelectedValidasiSupplier,
  };
};

const server = process.env.REACT_APP_BACKEND_URL;
class validasiSupplier extends Component {
  constructor(props) {
    super(props);
    this.state = {
      columns: [
        {
          dataField: "tanggal",
          text: "Tanggal",
        },

        {
          dataField: "no_bon",
          text: "Nomor Bon",
        },
        {
          dataField: "kode_supplier",
          text: "Supplier",
        },
        {
          dataField: "keterangan",
          text: "Keterangan",
        },

        {
          dataField: "total_bruto",
          text: "Total Bruto",
        },
        {
          dataField: "total_netto",
          text: "Total Netto",
        },
        {
          dataField: "bon_rp",
          text: "Bon RP",
        },
        {
          dataField: "titip_gr",
          text: "Titip Gr",
        },
        {
          dataField: "titip_rp",
          text: "Titip Rp",
        },
      ],

      step: 0,
      step1: "row",
      step2: "row d-none",
      step3: "row d-none",
    };
  }

  updateButton(data) {
    switch (data) {
      case "Penerimaan Barang Supplier":
        this.props.dispatch(getValidasiSupplierPenerimaanBarangSupplier());
        break;
      case "Return Supplier":
        this.props.dispatch(getValidasiSupplierReturnSupplier());
        break;
      case "Tolakan Return Supplier":
        this.props.dispatch(getValidasiSupplierTolakanReturnSupplier());
        break;
      case "Pembayaran Hutang Supplier":
        this.props.dispatch(getValidasiSupplierPembayaranHutangSupplier());
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        this.props.dispatch(getValidasiSupplierTitipSupplier());
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  disabledButton = (e) => {
    e.target.disabled = true;
  };

  changeTransaksi = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
    switch (e.target.value) {
      case "Penerimaan Barang Supplier":
        this.props.dispatch(getValidasiSupplierPenerimaanBarangSupplier());
        break;
      case "Return Supplier":
        this.props.dispatch(getValidasiSupplierReturnSupplier());
        break;
      case "Tolakan Return Supplier":
        this.props.dispatch(getValidasiSupplierTolakanReturnSupplier());
        break;
      case "Pembayaran Hutang Supplier":
        this.props.dispatch(getValidasiSupplierPembayaranHutangSupplier());
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        this.props.dispatch(getValidasiSupplierTitipSupplier());
        break;
      default:
        console.log("ERROR");
        break;
    }
  };
  sendValidasi(data) {
    var hasil = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
    var kirim = {
      trx_supplier: JSON.parse(localStorage.getItem("trx_supplier")) || [],
    };
    switch (data) {
      case "Penerimaan Barang Supplier":
        AxiosMasterPut("validate-supplier/validate", kirim)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPenerimaanBarangSupplier()
                )
              )
          )
          .catch((err) => gagal("Validasi Gagal"));

        break;
      case "Return Supplier":
        Axios.put(server + "spp/dc/rtr", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPenerimaanBarangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      case "Tolakan Return Supplier":
        Axios.put(server + "spp/dc/tlkrtr", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierTolakanPembayaranSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      case "Pembayaran Hutang Supplier":
        Axios.put(server + "validate-supplier/validate", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPembayaranHutangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      case "Tolakan Pembayaran Supplier":
        this.props.dispatch(getValidasiSupplierTolakanPembayaranSupplier());
        break;
      case "Titip Supplier":
        Axios.put(server + "spp/dc/ttp", hasil)
          .then((res) =>
            Swal.fire({
              position: "top-right",
              icon: "success",
              text: "Validasi Berhasil",
              showConfirmButton: false,
              timer: 1500,
            })
              .then(() => localStorage.removeItem("FakturTerpilih"))
              .then(() => this.prevStep())
              .then(() =>
                this.props.dispatch(
                  getValidasiSupplierPembayaranHutangSupplier()
                )
              )
          )
          .catch((err) =>
            Swal.fire({
              position: "top-right",
              icon: "error",
              text: "Tampaknya ada sedikit kendala, Mohon Periksa Koneksi Anda",
              showConfirmButton: false,
              timer: 2500,
            })
          );
        break;
      default:
        console.log("ERROR");
        break;
    }
  }
  nextStep = () => {
    const { step } = this.state;
    if (step === 0) {
      this.setState({ step: step + 1, step2: "row", step1: "row d-none" });
    }
    this.props.dispatch(getDataSelectedValidasiSupplier());
  };
  prevStep = () => {
    const { step } = this.state;
    if (step === 1) {
      this.setState({ step: step - 1, step2: "row d-none", step1: "row" });
    }
  };
  changeInput = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };
  render() {
    const selectRow = {
      mode: "checkbox",
      clickToSelect: true,
      onSelect: (row, isSelect, rowIndex, e) => {
        console.log(row.noFaktur);
        let array = JSON.parse(localStorage.getItem("FakturTerpilih")) || [];
        let trx_supplier =
          JSON.parse(localStorage.getItem("trx_supplier")) || [];
        const data = {
          tanggal: row.tanggal,
          no_bon: row.no_bon,
          no_trx: row.no_trx,
          kode_supplier: row.kode_supplier,
          total_bruto: row.total_bruto,
          total_netto: row.total_netto,
          bon_rp: row.bon_rp,
          titip_gr: row.titip_gr,
          titip_rp: row.titip_rp,
          keterangan: row.keterangan,
        };
        let validasi = {
          no_trx: row.no_trx,
          keterangan: row.keterangan,
        };
        if (isSelect) {
          var index1 = array.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          var index2 = trx_supplier.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          if (index1 < 0) {
            array.push(data);
            trx_supplier.push(validasi);
          } else {
            array.splice(index1, 1);
            array.splice(index2, 1);
          }
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("trx_supplier", JSON.stringify(trx_supplier));
        } else {
          var index3 = array.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          var index4 = trx_supplier.findIndex((item, i) => {
            return item.no_trx === row.no_trx;
          });
          array.splice(index3, 1);
          array.splice(index4, 1);
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
          localStorage.setItem("trx_supplier", JSON.stringify(trx_supplier));
        }
      },
      onSelectAll: (isSelect, rows, e) => {
        var array = [];
        rows.forEach(function (row) {
          var data = {
            tanggal: row.tanggal,
            no_bon: row.no_bon,
            kode_supplier: row.kode_supplier,
            total_bruto: row.total_bruto,
            total_netto: row.total_netto,
            bon_rp: row.bon_rp,
            titip_gr: row.titip_gr,
            titip_rp: row.titip_rp,
            keterangan: row.keterangan,
          };
          array.push(data);
        });
        if (isSelect) {
          localStorage.setItem("FakturTerpilih", JSON.stringify(array));
        } else {
          localStorage.removeItem("FakturTerpilih");
        }
      },
    };
    return (
      <div className="container-fluid">
        <div className="card">
          <HeaderCard title="Validasi Supplier" />
          <div className="card-body">
            <div className="col-lg-12">
              <div className="row">
                <div className="col-lg-12 col-md-12">
                  <Stepper
                    steps={[
                      { title: "Pilih Data" },
                      { title: "Validasi Data" },
                      // { title: "Data Rekening" },
                    ]}
                    activeStep={this.state.step}
                  />
                </div>
                <div className="col-lg-12">
                  <div className={this.state.step1}>
                    <div className="col-lg-3">
                      <label htmlFor="">Pilih Jenis Transaksi</label>
                      <Field
                        name="tipeTransaksi"
                        label="Dari"
                        component="select"
                        className="form-control"
                        onChange={this.changeTransaksi("tipeTransaksi")}
                      >
                        <option value="DEFAULT" key="DEFAULT">
                          SILAHKAN PILIH
                        </option>
                        <option
                          value="Penerimaan Barang Supplier"
                          key="Penerimaan Barang Supplier"
                        >
                          Penerimaan Barang Supplier
                        </option>
                        <option value="Return Supplier" key="Return Supplier">
                          Return Supplier
                        </option>
                        <option
                          value="Tolakan Return Supplier"
                          key="Tolakan Return Supplier"
                        >
                          Tolakan Return Supplier
                        </option>
                        <option
                          value="Pembayaran Hutang Supplier"
                          key="Pembayaran Hutang Supplier"
                        >
                          Pembayaran Hutang Supplier
                        </option>
                        <option
                          value="Tolakan Pembayaran Supplier"
                          key="Tolakan Pembayaran Supplier"
                        >
                          Tolakan Pembayaran Supplier
                        </option>
                        <option value="Titip Supplier" key="Titip Supplier">
                          Titip Supplier
                        </option>
                      </Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="supplier"
                        label="Supplier"
                        component={renderField}
                        type="text"
                        onChange={this.changeInput("supplier")}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="noBon"
                        label="Nomor Bon"
                        component={renderField}
                        type="text"
                        onChange={this.changeInput("noBon")}
                      ></Field>
                    </div>
                    <div className="col-lg-3">
                      <Field
                        name="tanggal"
                        label="Tanggal Transaksi"
                        component={renderField}
                        type="date"
                        onChange={this.changeInput("tanggalFrom")}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button className="btn btn-primary">
                          <i className="fa fa-eye"></i> Lihat Data
                        </button>
                      </div>
                    </div>
                    <div className="col-lg-12 mt-3">
                      <GlobalTabel
                        keyField="no_bon"
                        data={this.props.listValidasiSupplier}
                        columns={this.state.columns}
                        search
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                        selectRow={selectRow}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.nextStep()}
                        >
                          Next <i className="fa fa-chevron-right ml-3"></i>
                        </button>
                      </div>
                    </div>
                  </div>
                  <div className={this.state.step2}>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="no_bon"
                        data={this.props.listDataSelectedValidasiSupplier}
                        columns={this.state.columns}
                        search
                        striped
                        hover
                        condensed
                        bordered={true}
                        headerClasses="header-table"
                        pagination={paginationFactory()}
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="row">
                        <div className="col-lg-6">
                          <div className="text-left">
                            <button
                              className="btn btn-primary"
                              onClick={() => this.prevStep()}
                            >
                              <i className="fa fa-chevron-left mr-3"></i>Back
                            </button>
                          </div>
                        </div>
                        <div className="col-lg-6">
                          <div className="text-right">
                            <button
                              className="btn btn-primary"
                              onClick={() =>
                                this.sendValidasi(this.state.tipeTransaksi)
                              }
                            >
                              <i className="fa fa-check-circle mr-3"></i>
                              VALIDASI
                            </button>
                          </div>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

validasiSupplier = reduxForm({
  form: "validasiSupplier",
  enableReinitialize: true,
})(validasiSupplier);
export default connect(maptostate, null)(validasiSupplier);
